.statistics-overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(0, 0, 0, 0.5);
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 1000;
}

.statistics-modal {
  background: white;
  border-radius: 8px;
  padding: 20px;
  max-width: 400px;
  width: 90%;
  max-height: 80vh;
  overflow-y: auto;
}

.statistics-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 20px;
}

.statistics-header h2 {
  margin: 0;
  font-size: 24px;
}

.close-button {
  background: none;
  border: none;
  font-size: 24px;
  cursor: pointer;
  padding: 0;
  width: 30px;
  height: 30px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.close-button:hover {
  background-color: #f0f0f0;
  border-radius: 50%;
}

.stats-grid {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  gap: 16px;
  margin-bottom: 30px;
}

.stat-item {
  text-align: center;
}

.stat-value {
  font-size: 28px;
  font-weight: bold;
  margin-bottom: 4px;
}

.stat-label {
  font-size: 12px;
  color: #666;
}

.guess-distribution h3 {
  margin: 0 0 16px 0;
  font-size: 16px;
}

.guess-row {
  display: flex;
  align-items: center;
  margin-bottom: 8px;
  gap: 8px;
}

.guess-number {
  width: 20px;
  font-size: 12px;
  font-weight: bold;
}

.guess-bar-container {
  flex: 1;
  height: 20px;
  background-color: #d3d6da;
  border-radius: 2px;
  overflow: hidden;
}

.guess-bar {
  height: 100%;
  transition: width 0.3s ease;
}

.guess-count {
  width: 30px;
  text-align: right;
  font-size: 12px;
  font-weight: bold;
}
